'use client';

import { useMemo, useState } from 'react';

type Status = 'READY' | 'DAY_PUBLISHED' | 'CLOSED';
type Side = 'day' | 'night';

type Props = {
  sessionDate?: string;
  // fallback string (kept for backward-compat)
  formatted?: string;
  // explicit fields (recommended)
  side?: Side;                        
  status?: Status;                    
  dayPanna?: string | null;
  dayDigit?: number | null;
  nightPanna?: string | null;
  nightDigit?: number | null;
  jodi?: string | null;               // optional (we can derive)
  onRefresh?: () => Promise<void> | void;
};

export default function ResultRibbon({
  sessionDate,
  formatted,
  side,
  status,
  dayPanna,
  dayDigit,
  nightPanna,
  nightDigit,
  jodi,
  onRefresh,
}: Props) {
  const [spinning, setSpinning] = useState(false);
  const refresh = async () => {
    if (!onRefresh) return;
    setSpinning(true);
    try { await onRefresh(); } finally { setSpinning(false); }
  };

  // decide what to show in the main line
  const mainLine = useMemo(() => {
    const dd = dayDigit ?? undefined;
    const nd = nightDigit ?? undefined;
    const dp = dayPanna ?? undefined;
    const np = nightPanna ?? undefined;

    const leftPanna  = side === 'night' && dp === undefined ? '‚Äî' : (dp ?? '‚Äî');
    const leftDigit  = side === 'night' && dd === undefined ? '‚Äî' : (dd ?? '‚Äî');
    const rightDigit = side === 'day'   && nd === undefined ? '‚Äî' : (nd ?? '‚Äî');
    const rightPanna = side === 'day'   && np === undefined ? '‚Äî' : (np ?? '‚Äî');

    // if side not provided, fall back to classic behavior (or formatted)
    if (!side && formatted) return formatted;

    return `(${leftPanna}) ${leftDigit} | ${rightDigit} (${rightPanna})`;
  }, [side, dayPanna, dayDigit, nightPanna, nightDigit, formatted]);

  const jodiText = useMemo(() => {
    // show jodi only when both digits exist
    if (dayDigit == null || nightDigit == null) return '‚Äî';
    return jodi ?? `${dayDigit}${nightDigit}`;
  }, [dayDigit, nightDigit, jodi]);

  return (
    <div className="rounded border-2 border-red-700 bg-yellow-300 text-center py-5 px-3 shadow-[inset_0_0_0_2px_rgba(255,0,0,0.3)]">
      <div className="flex items-center justify-center gap-2 mb-1">
        {side && (
          <span className={`inline-block text-xs font-extrabold px-2 py-0.5 rounded-full border ${
            side === 'day' ? 'bg-yellow-400 text-black border-yellow-700' : 'bg-purple-400 text-black border-purple-700'
          }`}>
            {side.toUpperCase()}
          </span>
        )}
        {status && (
          <span className={`inline-block text-xs font-bold px-2 py-0.5 rounded-full border ${
            status === 'READY'
              ? 'bg-blue-600/15 text-blue-900 border-blue-700/30'
              : status === 'DAY_PUBLISHED'
              ? 'bg-yellow-500/20 text-yellow-900 border-yellow-700/40'
              : 'bg-green-600/20 text-green-900 border-green-700/40'
          }`}>
            {status}
          </span>
        )}
      </div>

      {sessionDate && <p className="text-sm text-gray-700 mt-1">{sessionDate}</p>}

      <p className="mt-1 text-xl font-bold font-mono">{mainLine}</p>

      <p className="text-sm text-gray-700 mt-1">
        Jodi: <span className="font-semibold">{jodiText}</span>
      </p>

      {onRefresh && (
        <button
          onClick={refresh}
          className="mt-3 inline-block rounded-full bg-yellow-400/90 px-4 py-1.5 text-sm font-semibold shadow hover:bg-yellow-400 border border-yellow-600"
        >
          {spinning ? 'Refreshing‚Ä¶' : 'Refresh Result'}
        </button>
      )}
    </div>
  );
}
